.winner_item {
  position: relative;
  padding: 20px;
  /* border: solid 1px #ddd; */
  border-radius: 15px;
  -moz-border-radius: 15px;
  -webkit-border-radius: 15px;
  /* margin-bottom: 25px; */
  /* background: #ffffff; */
  margin: 0;
  margin-top: 0px;
}

.winner_item_wrap {
  width: 100%;
  text-align: center;
  border-color: #03a101;
  /* display: flex; */
  align-items: center;
  margin-top: 20px;
  margin-bottom: 0px;
  justify-content: center;
  position: relative;
}

.winner_item_extra {
  position: absolute;
  opacity: .5;
  width: 100%;
  height: 100%;
  z-index: 3;
  background: rgba(255, 255, 255, .8);
  border-radius: 5px;
  left: 0;
  display: table;
  transform: scale(1.03);
  -moz-backdrop-filter: saturate(180%) blur(6px);
  -webkit-backdrop-filter: saturate(180%) blur(6px);
  backdrop-filter: saturate(180%) blur(6px);
  background: rgba(black, .1);
  /* visibility: hidden; */
  border: 1px solid black;
}



