.buttontarget {
     
position: relative;
border-width: 0px;
border: none;
background: transparent;
padding: 0;
cursor: pointer;
outline-offset: 4px;
user-select: none;
touch-action: manipulation;
 border-radius: 2px;
}

.buttontarget:hover {
filter: brightness(110%);
border-width: 0px;
border: none;
}

.buttontarget:hover .verde {
    border-width: 0px;
    border: none;
}

.buttontarget:active .verde {
    border-width: 0px;
    border: none;
}



.buttontarget:focus:not(:focus-visible) {
outline: none;
border-width: 0px;
border: none;
}

